import * as React from 'react';

import { connect } from 'react-redux';
import { IStatus, StatusType } from '../../common/definitions';
import { IAppState } from '../store';
import { StatusChangedAction } from '../../common/redux/Status';
import { Notifications } from '../../common/Notifications';

interface IPropsFromState {
  status: IStatus;
}

interface IPropsFromDispatch {
  ready: () => void;
}

interface IOwnProps {}

const mapStateToProps = (state: IAppState) => ({
  status: state.status
});

const mapDispatchToProps = (dispatch: any, ownProps: IOwnProps) => ({
  ready: () =>
    dispatch(
      StatusChangedAction({
        type: StatusType.READY,
        statusMessage: ''
      })
    )
});

export const _Notifications = connect<IPropsFromState, IPropsFromDispatch, IOwnProps>(
  mapStateToProps,
  mapDispatchToProps
)(Notifications);
