import { IToken, TokenRequestError, TokenCache, TokenRequest, TokenRequestV2 } from 'pintra-fx';
import Axios from 'axios';
import { IEnv } from '../store';
import { IPersona } from '../../common/definitions';

export interface IGraphResult {
  users: any[];
  nextPageToken: string | null;
}

export class UserService {
  public async getUsers(env: IEnv, url: string, hasNext: boolean) {
    const tokenResponse: IToken | TokenRequestError = await TokenCache.getTokenV2(
      new TokenRequestV2('graphUserReadAll', 'https://graph.microsoft.com/User.Read.All'),
      {
        wpAjaxAdminUrl: env.wpAjaxAdminUrl,
        nonce: env.nonce
      }
    );

    if (tokenResponse instanceof Error) {
      throw 'Unexpected: Could not retrieve a valid access token.';
    }

    const token = <IToken>tokenResponse;
    let response: any;

    try {
      response = await Axios.get(url, {
        headers: {
          Accept: 'application/json',
          ContentType: 'application/json; odata=verbose',
          Authorization: `Bearer ${token.bearer}`
        }
      });
    } catch (error) {
      throw error;
    }

    if (response.data && response.data.value && Array.isArray(response.data.value)) {
      const nextLinkExists = response.data.hasOwnProperty('@odata.nextLink');

      return {
        users: [...response.data.value],
        nextPageToken: nextLinkExists ? response.data['@odata.nextLink'] : null
      };
    }

    throw 'Unexpected: Invalid graph data received.';
  }

  public parseEmployees(users: any[]): IPersona[] {
    let employees: IPersona[] = [];

    if (!Array.isArray(users)) {
      return employees;
    }

    for (let i = 0; i < users.length; i++) {
      employees.push({
        businessPhones: this.tryGetArrayUserProperty(users[i], 'businessPhones'),
        city: this.tryGetStringUserProperty(users[i], 'city'),
        companyName: this.tryGetStringUserProperty(users[i], 'companyName'),
        country: this.tryGetStringUserProperty(users[i], 'country'),
        department: this.tryGetStringUserProperty(users[i], 'department'),
        displayName: this.tryGetStringUserProperty(users[i], 'displayName'),
        employeeId: this.tryGetStringUserProperty(users[i], 'employeeId'),
        faxNumber: this.tryGetStringUserProperty(users[i], 'faxNumber'),
        givenName: this.tryGetStringUserProperty(users[i], 'givenName'),
        imAddress: this.tryGetStringUserProperty(users[i], 'imAddress'),
        jobTitle: this.tryGetStringUserProperty(users[i], 'jobTitle'),
        mail: this.tryGetStringUserProperty(users[i], 'mail'),
        mobilePhone: this.tryGetStringUserProperty(users[i], 'mobilePhone'),
        otherMails: this.tryGetArrayUserProperty(users[i], 'otherMails'),
        showInAddressList: this.tryGetBoolUserProperty(users[i], 'showInAddressList'),
        streetAddress: this.tryGetStringUserProperty(users[i], 'streetAddress'),
        surname: this.tryGetStringUserProperty(users[i], 'surname'),
        userPrincipalName: this.tryGetStringUserProperty(users[i], 'userPrincipalName')
      });
    }
    return employees;
  }

  private tryGetStringUserProperty(user: any, propertyName: string): string {
    if (propertyName in user) {
      return user[propertyName] ? user[propertyName] : '';
    }
    return '';
  }

  private tryGetArrayUserProperty(user: any, propertyName: string): string[] {
    if (propertyName in user) {
      return user[propertyName] ? user[propertyName] : [];
    }
    return [];
  }

  private tryGetBoolUserProperty(user: any, propertyName: string): boolean {
    if (propertyName in user) {
      return user[propertyName] ? user[propertyName] == 'true' : false;
    }
    return false;
  }
}
