import * as React from 'react';

import { SearchBox } from 'office-ui-fabric-react/lib/SearchBox';
import { Spinner, SpinnerSize } from 'office-ui-fabric-react/lib/Spinner';
import { IStatus, StatusType } from './definitions';
import { StyledSearchBoxRow, StyledSearchBoxWrapper } from './styled';

export interface ISearchBoxProps {
  query: (query: string) => void; // container
  status: IStatus;
}

export const ModernSearchBox: React.SFC<ISearchBoxProps> = props => {
  return (
    <StyledSearchBoxWrapper className={'pintraSearchBoxWrapper'}>
      <StyledSearchBoxRow className={'pintraSearchBoxRow'}>
        <SearchBox
          className={'pintraSearchBox'}
          placeholder="Search"
          onChange={newValue => props.query(newValue)}
          onSearch={newValue => props.query(newValue)}
          underlined={true}
        />
      </StyledSearchBoxRow>
      <StyledSearchBoxRow className={'pintraSearchBoxRow'}>
        {props.status.type === StatusType.FETCHING && (
          <Spinner
            className={'pintraSearchBoxSpinner'}
            style={{ paddingTop: '0.5rem' }}
            size={SpinnerSize.small}
          />
        )}
      </StyledSearchBoxRow>
    </StyledSearchBoxWrapper>
  );
};
