import { Reducer } from 'redux'

//#region actions

export interface IUpdateNextPageTokenAction {
  type: 'UPDATE_NEXT_PAGE_TOKEN'
  nextPageToken: string
}

export const UpdateNextPageTokenAction = (
  nextPageToken: string,
): IUpdateNextPageTokenAction => {
  return {
    type: 'UPDATE_NEXT_PAGE_TOKEN',
    nextPageToken,
  }
}

export type NextPageTokenAction = IUpdateNextPageTokenAction

//#endregion

//#region reducers

export const NextPageTokenReducer: Reducer<string> = (
  nextPageToken: string = 'https://graph.microsoft.com/beta/users?$top=3',
  action: NextPageTokenAction,
) => {
  switch (action.type) {
    case 'UPDATE_NEXT_PAGE_TOKEN':
      return action.nextPageToken
    default:
      return nextPageToken
  }
}

//#endregion
