import { IAppState, IEnv } from '.';
import { ActionCreator, Action } from 'redux';
import { ThunkAction, ThunkDispatch } from 'redux-thunk';
import { Reducer } from 'redux';
import { UserService, IGraphResult } from '../services/UserService';
import { UpdateHasNextAction } from './hasNext';
import { UpdateNextPageTokenAction } from './nextPageToken';
import { StatusChangedAction } from '../../common/redux/Status';
import { StatusType, IPersona } from '../../common/definitions';
import { getNumberOrDefault, asBool } from '../../utils/typeHelpers';

//#region actions

export interface IUpdateEmployeeesAction {
  type: 'UPDATE_EMPLOYEES';
  employees: IPersona[];
}

export const UpdateEmployeesAction = (employees: IPersona[]): IUpdateEmployeeesAction => {
  return {
    type: 'UPDATE_EMPLOYEES',
    employees: employees
  };
};

export interface IUpdateAvatarAction {
  type: 'UPDATE_AVATAR';
  index: number;
  contact: IPersona;
}

export const UpdateAvatarAction = (index: number, contact: IPersona): IUpdateAvatarAction => {
  return {
    type: 'UPDATE_AVATAR',
    index,
    contact
  };
};

export type EmployeesAction = IUpdateEmployeeesAction | IUpdateAvatarAction;

//#endregion

//#region thunk

export const EmployeesThunk: ActionCreator<ThunkAction<void, IAppState, void, Action>> = (
  env: IEnv,
  moreResults: boolean,
  filter?: string
) => {
  return (dispatch: ThunkDispatch<IAppState, void, Action>, getState: () => IAppState): void => {
    let minQueryLength = getNumberOrDefault(env.props.minQueryLength, 3);

    // Prepare for search
    if (
      !moreResults &&
      ((!!filter && filter.length >= minQueryLength) || (!filter && asBool(env.props.autoSearch)))
    ) {
      const pageSize = getNumberOrDefault(env.props.pageSize, 10);
      const filterExpression = !!filter
        ? `(startswith(displayName,'${filter}') or startswith(givenName,'${filter}') or startswith(surname,'${filter}') or startswith(mail,'${filter}') or startswith(userPrincipalName,'${filter}') or startswith(displayName,'${filter}'))`
        : '';
      const queryTemplateGlue = !!filter ? ` and ` : '';
      const queryTemplate = !!env.props.queryTemplate ? `${queryTemplateGlue}(${env.props.queryTemplate})` : '';

      dispatch(
        UpdateNextPageTokenAction(
          `https://graph.microsoft.com/beta/users?$filter=${filterExpression}${queryTemplate}&$top=${pageSize}`
        )
      );
      dispatch(UpdateEmployeesAction([]));
      dispatch(StatusChangedAction({ type: StatusType.READY, statusMessage: '' }));
    }
    // Min. Criteria to start a new search not met
    else if (!moreResults && ((!!filter && filter.length < minQueryLength) || !filter)) {
      dispatch(UpdateEmployeesAction([]));
      dispatch(StatusChangedAction({ type: StatusType.READY, statusMessage: '' }));
      return;
    }

    const { nextPageToken, hasNext, employees } = getState();

    if (!nextPageToken) {
      return;
    }

    const userService = new UserService();

    userService
      .getUsers(env, nextPageToken, hasNext)
      .then((graphResponse: IGraphResult) => {
        const { nextPageToken, users } = graphResponse as IGraphResult;
        const newEmployees = userService.parseEmployees(users);
        const employeesFromState = [...employees];

        for (let i = 0; i < newEmployees.length; i++) {
          employeesFromState.push(newEmployees[i]);
        }

        dispatch(UpdateEmployeesAction([...employeesFromState]));

        if (employeesFromState.length === 0) {
          dispatch(StatusChangedAction({ type: StatusType.NORESULTS, statusMessage: 'No results' }));
        }
        if (employeesFromState.length > 0) {
          dispatch(
            StatusChangedAction({
              type: StatusType.HASRESULTS,
              statusMessage: `About ${employeesFromState.length} results`
            })
          );
        }

        dispatch(UpdateHasNextAction(nextPageToken !== null));
        dispatch(UpdateNextPageTokenAction(nextPageToken));
      })
      .catch((err: any) => {
        dispatch(
          StatusChangedAction({
            type: StatusType.ERROR,
            statusMessage: !!err.message ? err.message : err.toString()
          })
        );
        dispatch(UpdateHasNextAction(false));
        console.log(err);
      });
  };
};

//#endregion

//#region reducer

export const EmployeesReducer: Reducer<IPersona[]> = (employees: IPersona[] = [], action: EmployeesAction) => {
  switch (action.type) {
    case 'UPDATE_EMPLOYEES':
      return [...action.employees];
    case 'UPDATE_AVATAR':
      const prevEmployees = [...employees];
      prevEmployees[action.index] = action.contact;
      return prevEmployees;
    default:
      return employees;
  }
};

//#endregion
