import { combineReducers } from 'redux';
import { EmployeesReducer } from './employees';
import { HasNextReducer } from './hasNext';
import { NextPageTokenReducer } from './nextPageToken';
import { IStatus, IPersona } from '../../common/definitions';
import { StatusReducer } from '../../common/redux/Status';

export interface IEnv {
  nonce: string;
  wpAjaxAdminUrl: string;
  props: IEnvProps;
}

export interface IEnvProps {
  resource?: string;
  pageSize?: string;
  minQueryLength?: string;
  addWildcard?: string;
  hideSearchBox?: string;
  hideMessageBar?: string;
  hidePaging?: string;
  primaryHtml?: string;
  secondaryHtml?: string;
  tertiaryHtml?: string;
  metaHtml?: string;
  showContactActionButtons?: string;
  emailProperty?: string;
  workPhoneProperty?: string;
  imProperty?: string;
  mobilePhoneProperty?: string;
  pluginServerRelativeUrl?: string;
  getUserPhoto?: string;
  queryTemplate?: string;
  autoSearch?: string;
}

export interface IAppState {
  employees: IPersona[];
  status: IStatus;
  nextPageToken: string;
  hasNext: boolean;
}

export const rootReducer = combineReducers<IAppState>({
  employees: EmployeesReducer,
  status: StatusReducer,
  hasNext: HasNextReducer,
  nextPageToken: NextPageTokenReducer
});
