export const asBool = (value: any): boolean => {
  return typeof value === 'undefined'
    ? false
    : value === '1' || value.toString().toLowerCase() === 'true' || value.toString().toLowerCase() === 'yes';
};

export const getStringOrDefault = (value: any, defaultValue: string) =>
  !!value && typeof value === 'string' ? value : defaultValue;

export const getNumberOrDefault = (value: any, defaultValue: number) =>
  !!value && !isNaN(parseInt(value)) ? parseInt(value) : defaultValue;

export const getArrayOrDefault = (value: any, defaultValue: any[]) =>
  !!value && Array.isArray(value) ? value : defaultValue;
