import styled from 'styled-components'

export const StyledActionButtonGroup = styled.div`
  display: -webkit-flex; /* Safari */
  -webkit-flex-direction: column; /* Safari 6.1+ */
  display: flex;
  flex-direction: column;
`

export const StyledActionButtonWrapper = styled.div`
  -webkit-flex-wrap: wrap; /* Safari 6.1+ */
  flex-wrap: wrap;
  -webkit-flex-grow: 1;
  flex-grow: 1;
  -webkit-flex-shrink: 0;
  flex-shrink: 0;
  font-size: 12px;
  line-height: 24px;
  height: 24px;
  display: -webkit-flex; /* Safari */
  display: flex;
  cursor: pointer;
`

export const StyledActionButtonLabel = styled.div`
  font-size: 12px;
  line-height: 24px;
  height: 24px;
`

export const StyledActionButtonIcon = styled.div`
  font-size: 12px;
  line-height: 24px;
  height: 24px;
`

export const StyledSearchBoxWrapper = styled.div`
  display: flex;
  width: 100%;
`

export const StyledSearchBox = styled.div`
  width: calc(100% - 28px);
`

export const StyledSearchSpinner = styled.div`
  width: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
`
