import { Reducer } from 'redux';
import { IStatus, StatusType } from '../definitions';

// Redux Actions

export enum StatusActionType {
  READY = 'READY',
  NORESULTS = 'NORESULTS',
  FETCHING = 'FETCHING',
  ERROR = 'ERROR'
}

export interface IStatusChangedAction {
  type: StatusActionType;
  status: IStatus;
}

export type StatusAction = IStatusChangedAction;

/**
 * Status changed
 *
 */
export const StatusChangedAction = (newStatus: IStatus): IStatusChangedAction => {
  switch (newStatus.type) {
    case StatusType.READY:
      return { type: StatusActionType.READY, status: newStatus };
    case StatusType.NORESULTS:
      return { type: StatusActionType.NORESULTS, status: newStatus };
    case StatusType.FETCHING:
      return { type: StatusActionType.FETCHING, status: newStatus };
    case StatusType.NOTSUPPORTED:
      return { type: StatusActionType.NORESULTS, status: newStatus };
    default:
      return { type: StatusActionType.ERROR, status: newStatus };
  }
};

// Redux Reducer

export const StatusReducer: Reducer<IStatus> = (
  state: IStatus = { type: StatusType.READY, statusMessage: '' },
  action: StatusAction
) => {
  if (action.status) {
    return action.status;
  }
  return state;
};
