import { Reducer } from 'redux'

//#region actions

export interface IUpdateHasNextAction {
  type: 'UPDATE_HAS_NEXT'
  hasNext: boolean
}

export const UpdateHasNextAction = (hasNext: boolean): IUpdateHasNextAction => {
  return {
    type: 'UPDATE_HAS_NEXT',
    hasNext,
  }
}

export type HasNextAction = IUpdateHasNextAction

//#endregion

//#region reducers

export const HasNextReducer: Reducer<boolean> = (
  hasNext: boolean = true,
  action: HasNextAction,
) => {
  switch (action.type) {
    case 'UPDATE_HAS_NEXT':
      return action.hasNext
    default:
      return hasNext
  }
}

//#endregion
