import { getStringOrDefault } from './typeHelpers';
import { getExpandedProperty } from '../common/PersonaHelpers';

export async function tryReplaceTemplateTags(
  input: string,
  source: { [key: string]: string },
  env: { wpAjaxAdminUrl: string; nonce: string; [key: string]: string } = null,
  userName: string = ''
) {
  if (!input) {
    return '';
  }
  const regex = /(\__##.*?##__)/g;
  const tags = input.match(regex);
  for (let i = 0; i < tags.length; i++) {
    const templateTag = tags[i];
    const tag = templateTag.substring(4, templateTag.length - 4);
    let value = '';
    if (tag == 'manager' && !!userName) {
      try {
        value = await getExpandedProperty(userName, { expandableProperty: 'manager', key: 'displayName' }, env);
      } catch (err) {
        console.log(err);
      }
    } else {
      value = tag in source ? getStringOrDefault(source[tag], '') : '';
    }

    input = input.replace(templateTag, value);
  }
  return input;
}
