import { IEnv, IEnvProps } from '../ContentBySearch/store';
import { IToken, TokenCache, TokenRequestV2, TokenRequestError } from 'pintra-fx';
import Axios from 'axios';
import { asBool } from '../utils/typeHelpers';

export async function getDataUrl(userName: string, env: IEnv, callback: (dataUrl: string) => void) {
  if (!asBool(env.props.getUserPhoto)) {
    return;
  }

  const tokenResponse: IToken | TokenRequestError = await TokenCache.getTokenV2(
    new TokenRequestV2('graphUserReadAll', 'https://graph.microsoft.com/User.Read.All'),
    {
      wpAjaxAdminUrl: env.wpAjaxAdminUrl,
      nonce: env.nonce
    }
  );

  if (tokenResponse instanceof Error) {
    return new Error('Unexpected: Could not retrieve a valid access token.');
  }

  const token = tokenResponse as IToken;

  try {
    await Axios.get(`https://graph.microsoft.com/beta/users/${userName}/photos/48x48/$value`, {
      responseType: 'blob',
      headers: {
        Authorization: `Bearer ${token.bearer}`
      }
    })
      .then(
        response => {
          let reader = new FileReader();
          reader.readAsDataURL(response.data);
          reader.onload = () => {
            callback(reader.result as string);
          };
        },
        (error: any) => {
          throw error; // rethrow so its caught
        }
      )
      .catch((error: any) => console.log(error));
  } catch (error) {
    // return error;
  }
}

export async function getExpandedProperty(
  userName: string,
  prop: { expandableProperty: string; key: string },
  env: { wpAjaxAdminUrl: string; nonce: string; [key: string]: string }
) {
  const tokenResponse: IToken | TokenRequestError = await TokenCache.getTokenV2(
    new TokenRequestV2('graphUserReadAll', 'https://graph.microsoft.com/User.Read.All'),
    {
      wpAjaxAdminUrl: env.wpAjaxAdminUrl,
      nonce: env.nonce
    }
  );

  if (tokenResponse instanceof Error) {
    console.log(tokenResponse);
    throw 'Unexpected: Could not retrieve a valid access token.';
  }

  const token = tokenResponse as IToken;

  let response: any;

  try {
    response = await Axios.get(`https://graph.microsoft.com/beta/users/${userName}/${prop.expandableProperty}`, {
      headers: {
        Accept: 'application/json',
        ContentType: 'application/json; odata=verbose',
        Authorization: `Bearer ${token.bearer}`
      }
    });
  } catch (error) {
    // throw error;
  }

  if (response.data && response.data[prop.key]) {
    return response.data[prop.key];
  }
  throw `Unexpected: Could not retrieve expanded property ${prop.expandableProperty}`;
}
