import * as React from 'react';
import { IconNames } from '@uifabric/icons/lib/IconNames';
import {
  StyledActionButtonGroup,
  StyledActionButtonWrapper,
  StyledActionButtonLabel,
  StyledActionButtonIcon
} from '../EmployeeDirectory/components/styled';
import { Icon, IconType } from 'office-ui-fabric-react/lib/Icon';
import { IPersona } from './definitions';

export interface PersonaActionButtonsProps {
  workPhoneProperty?: string;
  mobilePhoneProperty?: string;
  emailProperty?: string;
  imProperty?: string;
  item: any;
}

export const PersonaActionButtons = (props: PersonaActionButtonsProps) => {
  const buttonDefinitions: {
    icon: string;
    tooltip: string;
    label: string;
    onClick: () => void;
  }[] = [];

  const firstOrStringOrNull = (test: any) => {
    let result = '';
    if (!!test) {
      if (Array.isArray(test) && test.length > 0) {
        result = test[0];
      } else if (!Array.isArray(test)) {
        result = test;
      }
    }
    return result;
  };

  if (props.emailProperty && props.emailProperty in props.item) {
    const email = firstOrStringOrNull(props.item[props.emailProperty]);
    if (!!email) {
      buttonDefinitions.push({
        icon: IconNames.Mail,
        tooltip: 'Email',
        label: email,
        onClick: () => window.open(`mailto:${email}`, '_blank')
      });
    }
  }

  if (props.imProperty && props.imProperty in props.item) {
    const imAddress = firstOrStringOrNull(props.item[props.imProperty]);
    if (!!imAddress) {
      buttonDefinitions.push({
        icon: IconNames.SkypeForBusinessLogo,
        tooltip: 'Skype',
        label: imAddress,
        onClick: () => window.open(`sip:${imAddress}`, '_blank')
      });
    }
  }

  if (props.mobilePhoneProperty && props.mobilePhoneProperty in props.item) {
    const mobilePhone = firstOrStringOrNull(props.item[props.mobilePhoneProperty]);
    if (!!mobilePhone) {
      buttonDefinitions.push({
        icon: IconNames.CellPhone,
        tooltip: 'CellPhone',
        label: mobilePhone,
        onClick: () => window.open(`tel:${mobilePhone}`, '_blank')
      });
    }
  }

  if (props.workPhoneProperty && props.workPhoneProperty in props.item) {
    const workPhone = firstOrStringOrNull(props.item[props.workPhoneProperty]);
    if (!!workPhone) {
      buttonDefinitions.push({
        icon: IconNames.Phone,
        tooltip: 'Phone',
        label: workPhone,
        onClick: () => window.open(`tel:${workPhone}`, '_blank')
      });
    }
  }

  return (
    <StyledActionButtonGroup className={'pintraActionButtonGroup'}>
      {buttonDefinitions.map(buttonDefinition => {
        return (
          <StyledActionButtonWrapper className={'pintraActionButtonWrapper'} onClick={buttonDefinition.onClick}>
            <StyledActionButtonIcon className={'pintraActionButtonIcon'}>
              <Icon
                iconName={buttonDefinition.icon}
                title={buttonDefinition.tooltip}
                ariaLabel={buttonDefinition.tooltip}
                style={{
                  fontSize: '12px',
                  lineHeight: '24px',
                  height: '24px',
                  width: '24px',
                  margin: 0,
                  padding: 0
                }}
              />
            </StyledActionButtonIcon>
            <StyledActionButtonLabel className={'pintraActionButtonLabel'}>
              {buttonDefinition.label}
            </StyledActionButtonLabel>
          </StyledActionButtonWrapper>
        );
      })}
    </StyledActionButtonGroup>
  );
};
