import styled from 'styled-components';

export const StyledSearchBoxWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
`;

export const StyledSearchBoxRow = styled.div``;

export const StyledPageButton = styled.button`
  font-family: 'Segoe UI', 'Segoe UI Web (West European)', 'Segoe UI', -apple-system, BlinkMacSystemFont, Roboto,
    'Helvetica Neue', sans-serif;
  font-size: 0.6rem;
  border: 0px;
  background: transparent;
  background-color: transparent;
  box-shadow: inherit;
  color: inherit;
  box-sizing: border-box;
  width: 30px;
  padding: 0px;
  margin: 0px;
  height: 30px;
  font-weight: 400;
`;

export const StyledCurrentPageButton = styled.button`
  font-family: 'Segoe UI', 'Segoe UI Web (West European)', 'Segoe UI', -apple-system, BlinkMacSystemFont, Roboto,
    'Helvetica Neue', sans-serif;
  font-size: 0.6rem;
  text-decoration: underline;
  border: 0px;
  background: transparent;
  background-color: transparent;
  box-shadow: inherit;
  color: inherit;
  box-sizing: border-box;
  width: 30px;
  padding: 0px;
  margin: 0px;
  height: 30px;
  font-weight: 400;
  text-decoration: underline;
  font-weight: 600;
`;

export const StyledItemCell = styled.div`
  position: relative;
  outline: transparent;
  min-height: 54px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  &:hover {
    background: #eee;
  }
  &::selection {
    background-color: #bfdcea;
  }
`;

export const StyledItemIconWrapper = styled.div`
  width: 50px;
  height; 50px;
  flex-shrink: 0;
`;

export const StyledItemIcon = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  align-items: center;
`;

export const StyledItemContent = styled.div`
  margin-left: 10px;
  overflow: hidden;
  flex-grow: 1;
`;

export const StyledItemPrimaryText = styled.div`
  font-size: 21px;
  font-weight: 100;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`;

export const StyledItemSecondaryText = styled.div`
  width: 95%;
  font-size: 80%;
  font-weight: 200;
`;

export const StyledItemTertiaryText = styled.div`
  font-size: 60%;
`;

export const StyledItemMeta = styled.div`
  font-size: 50%;
  font-weight: 100;
  color: #d3d3d3;
  box-sizing: border-box;
  width: calc(100% - 50px);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`;

export const StyledItemExtra = styled.div`
  font-size: 80%;
`;

export const StyledItemPath = styled.div`
  font-size: 60%;
  font-weight: 200;
  box-sizing: border-box;
  width: calc(100% - 50px);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`;

export const StyledLink = styled.a`
  font-size: inherit;
  color: inherit;
  text-decoration: inherit;
  &:hover: {
    font-size: inherit;
    color: inherit;
    text-decoration: underline;
  }
  &:visited: {
    font-size: inherit;
    color: inherit;
    text-decoration: inherit;
  }
`;

export const StyledFooter = styled.div`
  padding: 1rem 20% 20px 60px;
`;

export const TwoColumnCollapsableGrid = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
`;

export const HalfWidthColumn = styled.div`
  flex-basis: 50%;
  flex-grow: 0;
  min-width: 300px;
`;
