import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { IReactDomConfig, AppLauncher } from 'pintra-fx';
import { EmployeeDirectory } from './components/EmployeeDirectory';
import { createStore, applyMiddleware } from 'redux';
import { rootReducer } from './store';
import thunk from 'redux-thunk';
import { Provider } from 'react-redux';
import logger from 'redux-logger';

const reactDomConfig: IReactDomConfig = AppLauncher.getReactDomConfig();

const store = createStore(rootReducer, applyMiddleware(thunk, logger));

const element: React.ReactElement<any> = (
  <div>
    <Provider store={store}>
      <EmployeeDirectory env={reactDomConfig.env} />
    </Provider>
  </div>
);

ReactDOM.render(element, document.getElementById(reactDomConfig.rootId));
