import * as React from 'react';

import { MessageBar, MessageBarType } from 'office-ui-fabric-react/lib/MessageBar';
import { IStatus, StatusType } from './definitions';
import { Link } from 'office-ui-fabric-react/lib/Link';

interface INotificationsProps {
  status: IStatus;
  ready: () => void;
}

export const Notifications: React.SFC<INotificationsProps> = props => {
  const resultsError = props.status.type === StatusType.ERROR;
  const noResultsVisible = props.status.type === StatusType.NORESULTS;
  const resultsInfoVisible = props.status.type === StatusType.HASRESULTS;
  const notSupportedVisible = props.status.type === StatusType.NOTSUPPORTED;
  const subText = props.status.statusMessage ? props.status.statusMessage : 'No information available';
  return (
    <React.Fragment>
      {noResultsVisible && (
        <MessageBar
          className={'pintraMessage'}
          messageBarType={MessageBarType.error}
          isMultiline={false}
          onDismiss={(ev: React.MouseEvent<HTMLButtonElement>) => props.ready()}
          dismissButtonAriaLabel="Close">
          {subText}
        </MessageBar>
      )}
      {resultsInfoVisible && (
        <MessageBar
          className={'pintraMessage'}
          messageBarType={MessageBarType.info}
          isMultiline={false}
          onDismiss={(ev: React.MouseEvent<HTMLButtonElement>) => props.ready()}
          dismissButtonAriaLabel="Close">
          {subText}
        </MessageBar>
      )}
      {resultsError && (
        <MessageBar
          className={'pintraMessage'}
          messageBarType={MessageBarType.error}
          isMultiline={true}
          onDismiss={(ev: React.MouseEvent<HTMLButtonElement>) => props.ready()}
          dismissButtonAriaLabel="Close">
          {subText}
        </MessageBar>
      )}
      {notSupportedVisible && (
        <MessageBar
          className={'pintraMessage'}
          messageBarType={MessageBarType.warning}
          isMultiline={false}
          onDismiss={(ev: React.MouseEvent<HTMLButtonElement>) => props.ready()}
          dismissButtonAriaLabel="Close">
          {subText}{' '}
          <Link href={'https://www.wpo365.com/'} target={'_blank'}>
            Visit website.
          </Link>
        </MessageBar>
      )}
    </React.Fragment>
  );
};
