import * as React from 'react';

import { connect, MergeProps } from 'react-redux';
import { EmployeesThunk } from '../store/employees';

import { FocusZone, FocusZoneDirection } from 'office-ui-fabric-react/lib/FocusZone';
import { List } from 'office-ui-fabric-react/lib/List';
import { PrimaryButton } from 'office-ui-fabric-react/lib/Button';
import { IEnv, IAppState } from '../store';
import { IEmployeeDirectoryProps } from './EmployeeDirectory';
import { IPersona } from '../../common/definitions';
import { _ModernSearchBox } from './_ModernSearchBox';
import { _Notifications } from './_Notifications';
import { StyledFooter } from '../../common/styled';

import { initializeIcons } from 'office-ui-fabric-react/lib/Icons';
import { ListItem } from './ListItem';
import { asBool } from '../../utils/typeHelpers';
initializeIcons(/* optional base url */);

export interface IStateProps {
  employees: IPersona[];
  nextPageToken: string;
  hasNext: boolean;
}

export interface IDispatchProps {
  loadMore: () => void;
  search: (filter: string) => void;
}

export interface IOwnProps {
  env: IEnv;
}

export interface IEmployeeDirectoryProps {
  env: IEnv;
  employees: IPersona[];
  hasNext: boolean;
  loadMore: () => void;
  search: (filter: string) => void;
}

export class _EmployeeDirectory extends React.Component<IEmployeeDirectoryProps, {}> {
  private selection: Selection;

  public constructor(props: IEmployeeDirectoryProps) {
    super(props);
    const { env, search } = this.props;
    asBool(env.props.autoSearch) === true && search('');
  }

  public render(): React.ReactElement<IEmployeeDirectoryProps> {
    const { hasNext, employees, env, loadMore } = this.props;
    return (
      <div>
        {asBool(env.props.hideSearchBox) === false && <_ModernSearchBox env={this.props.env} />}
        {asBool(env.props.hideMessageBar) === false && <_Notifications />}
        <FocusZone direction={FocusZoneDirection.vertical}>
          <List items={this.props.employees} onRenderCell={this.onRenderCell} className={'pintraEdList'} />
        </FocusZone>
        <StyledFooter>
          {asBool(env.props.hidePaging) === false && hasNext && !!employees && employees.length > 0 && (
            <PrimaryButton
              data-automation-id="test"
              text="More results"
              disabled={!this.props.hasNext}
              onClick={loadMore}
              allowDisabledFocus={true}
            />
          )}
        </StyledFooter>
      </div>
    );
  }

  private onRenderCell = (item: IPersona, index: number | undefined): JSX.Element => {
    const { env } = this.props;
    return <ListItem item={item} index={index} env={env} />;
  };
}

const mapStateToProps = (state: IAppState): IStateProps => ({
  employees: state.employees,
  nextPageToken: state.nextPageToken,
  hasNext: state.hasNext
});

const mapDispatchToProps = (dispatch: any, ownProps: IOwnProps): IDispatchProps => {
  return {
    search: (filter: string) => dispatch(EmployeesThunk(ownProps.env, filter)),
    loadMore: () => dispatch(EmployeesThunk(ownProps.env, true))
  };
};

const mergeProps = (
  stateProps: IStateProps,
  dispatchProps: IDispatchProps,
  ownProps: IOwnProps
): IEmployeeDirectoryProps => {
  const { nextPageToken, ...returnedStateProps } = stateProps;
  return {
    ...returnedStateProps,
    ...dispatchProps,
    ...ownProps
  };
};

export const EmployeeDirectory = connect<IStateProps, IDispatchProps, IOwnProps, IEmployeeDirectoryProps>(
  mapStateToProps,
  mapDispatchToProps,
  mergeProps
)(_EmployeeDirectory);
