export enum StatusType {
  READY = 'READY',
  NORESULTS = 'NORESULTS',
  FETCHING = 'FETCHING',
  ERROR = 'ERROR',
  HASRESULTS = 'HASRESULTS',
  NOTSUPPORTED = 'NOTSUPPORTED'
}

export interface IStatus {
  type: StatusType;
  statusMessage?: string;
}

export interface IPagination {
  page: number;
  hasNextPage: boolean;
  hasPreviousPage: boolean;
  totalPages: number;
}

export interface IPersona {
  businessPhones?: string[];
  city?: string;
  companyName?: string;
  country?: string;
  department?: string;
  displayName: string;
  employeeId?: string;
  faxNumber?: string;
  givenName: string;
  imAddress?: string;
  jobTitle?: string;
  mail?: string;
  mobilePhone: string;
  otherMails?: string[];
  showInAddressList?: boolean;
  streetAddress?: string;
  surname: string;
  userPrincipalName: string;
  thumbnail?: string;
  [key: string]: any;
}
